import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";

const Media = ({ itemData, wallID, ownerId, wall }) => {
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
  return <div className="tb_spt_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
    {autoPlay ?
      <Video VideoClass="tb_spt_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={130} muted={true} autoPlay={autoPlay} handleVideoEnded={() => { }} />
      :
      <Image ImageClass="tb_spt_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={130} />
    }
  </div>
}
export default Media
