import { useEffect } from "react";
import AuthorDetails from "./AuthorDetails";
import SinglePostShare from "./SinglePostShare";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import CTAButton from "../../Elements/CTA/CtaButton";
import SinglePostSocialActions from "./SinglePostSocialActions";

const Card = ({ itemData, personalization, ThemeRule, itemIndex, clickToShowPopUp, wallID, ownerId, onClickToCTA, wall, onClickPopUpSlider }) => {
  const isProduct = itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
  const isShopIcon = (isProduct && itemData.hotspot === 0) ? true : false
  const isHotspot = itemData.hotspot && isProduct ? true : false

  const overlayStyle = { backgroundColor: ThemeRule.cardColor }
  const mediaType = itemData.type !== 1 ? true : false;
  const ctaStatus = (itemData.cta && Object.keys(itemData.cta).length > 0 && itemData.cta.status) ? true : false;
  const actionStyle = {
    justifyContent: !itemData.share.status ? 'flex-end' : '',
    borderTop: `1px solid ${ThemeRule.fontColor}`
  }
  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  return <div id={`tb-spt-post-${itemData.id}`} className="tb_spt_post_wrapper" tb-network={itemData.network.id} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0} tabIndex={"0"} area-label={itemData.content} role="article">
    <div className="tb_spt_post_in" onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
      <div className="tb_spt_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
        {mediaType ? <Media itemData={itemData} wallID={wallID} wall={wall} ownerId={ownerId} /> : ''}
        <div className="tb_spt_post_details">
          <div className="tb_spt_contant_wrapper">
            <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} />
            <div className="tb_spt_content_wrap">
              <div className="tb_spt_content_wrap_in">
                {!ThemeRule.hideContent || itemData.type == 1 ? <PostContent contentClass={"tb_spt_content"} item={itemData} content={itemData.content} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} /> : ''}
                {ctaStatus ? <div className="tb_spt_post_cta"><CTAButton ctaClass={'tb_spt_post_cta_btn'} cta={itemData.cta} contentTitle={itemData.contentTitle} item={itemData} onClickToCTA={onClickToCTA} /> </div> : ''}
              </div>
              <div className="tb_spt_social_actions_container" style={actionStyle}>
                {itemData.share.status ? <SinglePostShare share={itemData.share} shareColor={ThemeRule.fontColor} /> : ''}
                {ThemeRule.socialAction ? <SinglePostSocialActions itemData={itemData} ThemeRule={ThemeRule} /> : ''}
              </div>
            </div>
          </div>
        </div>
        <div className="tb_spt_post_overlay" style={overlayStyle}><div></div></div>
      </div>
    </div>
  </div>
}
export default Card