import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import Network from "../../Elements/Network/Network";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const authorNameStyle = {
    color: ThemeRule.authorColor, fontFamily: ThemeRule.css_font
  };
  const sepratorStyle = {
    backgroundColor: ThemeRule.authorColor
  };
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  return (
    <div className="tb_spt_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
      <div className="tb_spt_author">


        {postAuthor ? <Author author={author} network={network} authorClass={'tb_spt_author_profile'} /> : ''}

        <div className="tb_spt_author_info">
          {postAuthor ? <div className="tb_spt_authorname" style={authorNameStyle}>{author.name}</div> : ''}
          <div className="tb_spt_post_info">
            {postAuthor ? <div className="tb_spt_username" style={authorNameStyle}>{username}</div> : ''}
            {authorSeprator ? <div className="tb_spt_seprator" style={sepratorStyle}> </div> : ''}
            {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_spt_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} authorNameStyle={authorNameStyle} /> : ''}
          </div>
        </div>
      </div>
      <div className="tb_spt_social_">
        <Network networkClass={'tb_spt_social_ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} />
      </div>
    </div>
  );
}
export default AuthorDetails